@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;600&display=swap);
.wrapper{background-color:#121212;color:#ffffff;height:100%;padding-right:10px;padding-left:10px;align-items:center;justify-content:center}.header{display:flex;align-items:center;justify-content:center;min-height:90px}.thumbnail{position:absolute;top:0;z-index:-5000;width:0px;height:0px}.thumbnail img{object-fit:cover;width:0px;height:0px}.header h1{font-size:35px}.content{display:flex;align-items:center;justify-content:center;flex-wrap:wrap;margin:auto;width:80%}.launch{display:flex;align-items:center;flex-direction:column;justify-content:center;flex-wrap:wrap;width:70%}button{margin-top:10px;background-color:#05c790;border:none;cursor:pointer;color:white;padding:5px 12px;text-align:center;text-decoration:none;display:inline-block;font-size:15px;font-family:Poppins;border-radius:6px}button:hover{background-color:#1DF9BA}.launch img{display:block;margin-bottom:10px;margin-left:auto;margin-right:auto;border-radius:1%}.rockets{text-align:center;margin-bottom:30px}.description{margin-top:5px;margin-bottom:5px}*{box-sizing:border-box;margin:0}html{height:100%}body{font-family:"Poppins",sans-serif;align-items:center;justify-content:center}h1{font-weight:300;text-align:center;margin-bottom:5px}h1 span{display:block}.countdown{display:flex;align-items:center;justify-content:center;flex-wrap:wrap}.countdown__box svg{position:relative}.countdown__box svg circle{transform:rotate(-90deg);transform-origin:center;fill:transparent}.countdown__box_circles{position:relative}.countdown__box_left{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);font-weight:600;font-size:20px}.countdown__box_label{font-size:14px;font-weight:300;text-align:center}@media screen and (max-width: 840px){.wrapper{background-size:0px}.launch{min-width:270px}img{width:270px}header{flex-flow:column nowrap;padding-bottom:20px}}

